body{
    
}

.important{
    text-align: center;
    margin: 0 0 3vw;
}

.text{
    margin: 0 auto;
    width: 70%;
    padding: 2vw;
    border: rgba(10, 88, 80, 0.84) 2px solid;
    height: 30vw;
    overflow-y:scroll;
    
}

.message span {
    overflow-wrap: break-word;
}

.message{
    margin: 0 0 1vw 0;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgb(231, 231, 231); 
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(169, 169, 169); 
    border-radius: 10px;
  }

